.tribe-events-promo,
#tribe-bar-collapse-toggle,
.tribe-events-ical,
.event-is-recurring {
	display: none;
}

.content {

	.tribe-events-ical {
		display: none!important;
	}

	.tribe-events-notices {
	    background: $tribe;
	    border: 1px solid $tribe;
	    color: $text;
		margin: 10px 0 18px;
		padding: 12px 35px 12px 14px;
		text-shadow: 0 1px 0 transparent;
		border-radius: 0px;
	}

	.tribe-bar-submit {
		float: right;
		margin-left: 0px;
	}

	.tribe-events-page-title {
		margin-bottom: 40px;

		a {
			color: $text;
			text-decoration: none;
		}
	}

	.recurring-info-tooltip,
	.tribe-events-calendar .tribe-events-tooltip,
	.tribe-events-shortcode.view-week .tribe-events-tooltip,
	.tribe-events-week .tribe-events-tooltip {
		padding: 14px 20px 0;

		h3 {
			font-size: 22px;
		}

		p {
			font-size: 18px;
		}
	}

	.tribe-events-list-separator-month {
	    background-color: transparent;
	    display: block;
	    margin: 0;
	    padding: 0;
	    text-transform: none;

		color: #94827B;
		font-size: 30px;
		font-weight: 400;
		margin-bottom: 20px;
		font-family: $font1;
	}
}
